/* ========== General ========== */
.admin-dashboard {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 2rem 2.5rem;
    font-family: 'Segoe UI', sans-serif;
    background-color: #f4f5f7;
    border-radius: 1.5rem;
  }
  
  /* ========== Cards Row ========== */
  .Info-cards-row {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
  }
  
  /* ========== InfoCard ========== */
  .Info-card {
    flex: 1;
    min-width: 295px;
    background-color: #fff;
    border-radius: 1.25rem;
    padding: 1.8rem;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.06);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .Info-card-title {
    font-size: 1rem;
    color: #6b7280;
    font-weight: 500;
    margin-bottom: 0.25rem;
  }
  
  .Info-card-value {
    font-size: 2.2rem;
    font-weight: bold;
    color: #111827;
  }
  
  .Info-card-status {
    font-size: 0.875rem;
    color: #10b981;
    margin-top: 0.5rem;
    font-weight: 500;
  }
  
  .Info-card-buttons {
    margin-top: 1rem;
    display: flex;
    gap: 0.5rem;
    flex-wrap: wrap;
  }
  
  .Info-card-btn {
    background-color: #FFC8A3;
    color: #111827;
    border: 1px solid #FFC8A3;
    padding: 0.3rem 0.5rem;
    border-radius: 0.5rem;
    font-size: 0.8rem;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .Info-card-btn:hover {
    background-color: #FFEEE3;
  }
  
  /* ========== Activity Chart ========== */
  .activity-chart-container {
    background-color: #ffffff;
    padding: 2rem;
    border-radius: 1.25rem;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.06);
  }
  
  .activity-chart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .activity-chart-header h3 {
    font-size: 1.25rem;
    font-weight: 600;
    color: #111827;
  }
  
  .activity-chart-dropdown {
    padding: 0.5rem 0.8rem;
    border-radius: 0.5rem;
    border: 1px solid #d1d5db;
    font-size: 0.875rem;
    background-color: #f9fafb;
  }
  
  /* Bar Chart Placeholder (simplified version) */
  .bar-row {
    display: flex;
    gap: 0.5rem;
    align-items: flex-end;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  
  .bar-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.3rem;
  }
  
  .bar {
    width: 10px;
    border-radius: 4px;
  }
  
  .bar.yoga {
    height: 40px;
    background-color: #f59e0b;
  }
  
  .bar.meditation {
    height: 60px;
    background-color: #3b82f6;
  }
  
  .bar.aerobics {
    height: 30px;
    background-color: #10b981;
  }
  
  .bar-label {
    font-size: 0.75rem;
    color: #6b7280;
  }
  
  /* ========== Legend ========== */
  .activity-chart-legend {
    display: flex;
    gap: 1.5rem;
    font-size: 0.875rem;
    color: #6b7280;
    padding-top: 0.5rem;
  }
  
  .legend::before {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 3px;
    margin-right: 0.4rem;
  }
  
  .legend.yoga::before {
    background-color: #f59e0b;
  }
  
  .legend.meditation::before {
    background-color: #3b82f6;
  }
  
  .legend.aerobics::before {
    background-color: #10b981;
  }
  
  /* ========== Appointment Card ========== */
  .appointment-card {
    background-color: #fff;
    border-radius: 1.25rem;
    padding: 1.5rem 2rem;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.06);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .appointment-title {
    font-size: 1.125rem;
    font-weight: 600;
    color: #111827;
  }
  
  .appointment-Info {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 0.875rem;
    color: #6b7280;
  }
  
  .appointment-date {
    background-color: #c5f0ff;
    color: #1e3a8a;
    font-weight: 600;
    padding: 0.3rem 0.8rem;
    border-radius: 0.5rem;
    font-size: 0.75rem;
  }
  