@import url('https://fonts.googleapis.com/css2?family=Inria+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  max-height: 80%;
  overflow-y: auto;
  border-radius: 10px;
  width: 80%;
  max-width: 800px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  position: relative;
}

.modal-close {
  position: absolute;
  top: 8px;
  right: 12px;
  font-size: 20px;
  cursor: pointer;
}

.studentlistcontainer {
    width: 100%;
    height: auto;
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-family: "Inria Sans", serif;
}

.studentlistcontainer .student {
    width: 100%;
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
}

.studentlistcontainer .student .profile {
    display: flex;
    align-items: center;
    width: 40%; /* Adjust this as needed */
    min-width: 300px; /* Ensures the profile section has a minimum width */
}

.studentlistcontainer .student .profile img {
    width: 50px;
    height: 50px;
    margin-right: 15px; /* Increased margin for better spacing */
    flex-shrink: 0; /* Prevents image from shrinking */
}

.studentlistcontainer .student .profile .info {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.studentlistcontainer .student .profile .info span {
    font-size: 18px;
    color: #112b3c;
    font-weight: bold;
}

.studentlistcontainer .student .profile .info p {
    font-size: 16px;
    color: #999;
    margin: 0;
}

.studentlistcontainer .student .status {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #225375;
}

.studentlistcontainer .student .status.active {
    color: #ff6000;
}

.studentlistcontainer .student .actions {
    display: flex;
    gap: 10px;
}

.studentlistcontainer .student .actions button {
    border: 1px solid #ff6000;
    background-color: transparent;
    color: #ff6000;
    border-radius: 50px;
    padding: 5px 15px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;
}

.studentlistcontainer .student .actions button:hover {
    background-color: #ff6000;
    color: #fff;
}
