.min-max-form {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 24px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  margin: 20px auto;
}

.min-max-form h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

.min-max-form .form-group {
  margin-bottom: 15px;
}

.min-max-form label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #555;
}

.min-max-form input {
  width: 90%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.min-max-form .submit-btn {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #FFC8A3;
  color: black;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.min-max-form .submit-btn:hover {
  background-color: #FFEEE3;
}

.min-max-form .form-message {
  text-align: center;
  color: #d9534f;
  margin-top: 10px;
}